body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .App {
    text-align: center;
  }

  .App-logo {
    height: 30vmin;
    pointer-events: none;
    margin: 30px auto;
  }

  select {
    text-align: center;
    width: 90%;
    padding: 10px;
    margin: 10px auto;
    border-radius: 10px;
  }

  input {
    text-align: center;
    width: 90%;
    padding: 10px;
    margin: 10px auto;
    border-radius: 10px;
  }

  button {
    text-align: center;
    width: 90%;
    padding: 10px;
    margin: 10px auto;
    border-radius: 10px;
  }

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
