@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #000000 !important;
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  margin: 30px auto;
}

select {
  text-align: center;
  width: 90%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 10px;
}

input {
  text-align: center;
  width: 90%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 10px;
}

button {
  text-align: center;
  width: 90%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 10px;
}
